td, th {
  font-size: 14px;
}

.modal-content {
  width: 90vw;
  margin-left: -20vw;
}

.page-modal tr:focus {
  outline: 0;
}

.page-modal td, tr {
  word-wrap: break-word;
  white-space: nowrap;
}

.page-modal>div {
  max-height: 70vh;
  overflow-y: auto;
}

.page-modal table tr {
  line-height: 0;
  min-height: 0px;
  height: 0px;
  padding: 0;
  margin: 0;
}

.page-modal table tr td {
  font-size: 12px;
  padding: 8px;
  margin: 8px;
}

.logo-height {
  height: 60px;
}

.cursor-pointer {
  cursor: pointer;
}

.modal-content table input, .modal-content table select {
  width: 120px;
  height: 17px;
  padding: 0;
  font-size: 13px;
  text-align: center
}